.documentation-hero {
  background-color: nth($mz-pinks, 2);
  text-align: center;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  h1 {
    
  }
  .documentation-header {
    /* former _documentation.scss documentation-hero h1*/
    padding-top: 20px;
    color: #fff;
    /* former _documentation.scss h1*/
    font-weight: $display-font-weight-bold;
    font-family: $display-font-family;
    /* former _typography.scss */
    font-size: 48px;
    line-height: 1.2em;
    /* scale back margins on mobile */
    @media (max-width: $screen-sm) {
      font-size: 42px;
    }
  }
  p {
    margin-top: 24px;//1em;
  }
}

.documentation-nav {
  position: relative;
  z-index: 20;
}

.documentation-container {
  padding-top: 2em;
}

.documentation-content {
  line-height: 1.8;
  font-size: 16px;

  word-wrap: break-word;
  overflow-wrap: break-word;

  // Structural spacing
  section > p,
  section > ol,
  section > ul, {
    margin-bottom: 24px;//1em;
  }

  // Align last item to bottom of the container so navigation affixes to bottom cleanly
  > :last-child {
    margin-bottom: 0 !important;
  }

  img {
    max-width: 100%;
    display: inline;
    padding: 4px;
    line-height: 1.428571429;
    background-color: #fff;
  }

  // Override default img styles for .emoji
  img.emoji {
    padding: 0;
    background-color: transparent;
    line-height: inherit;
    max-width: initial;
  }

  // Control weird spacing on <p>'s generated inside lists
  li > p {
    margin-bottom: 0;
  }

  li > p + p {
    margin-top: 24px;//1em;
  }

  li > p > img {
    margin-top: 0;
    margin-bottom: 24px;//1em;
  }

  // Headings
  h1, h2, h3, h4, h5, h6 {
    position: relative;
  }

  /*smaller for documentation than styleguide*/
  h1 {
    font-size: 36px;
  }

  /*smaller for documentation than styleguide*/
  h2 {
    font-size: 24px;
  }

  /*smaller for documentation than styleguide*/
  h3 {
    font-size: 20px;
  }

  /*
  // allows hash links to navigate to proper location
  h1[id]:before,
  h2[id]:before,
  h3[id]:before,
  h4[id]:before,
  h5[id]:before,
  h6[id]:before {
    content: '';
    display: block;
    margin-top: -70px;
    height: 70px;
    pointer-events: none;
  }
  */

  // Each header's content is wrapped in a <section> container
  // This allows this to container to make anchor links navigate
  // without headbutting the top of the window, like so.
  // This requires the mdx_outline extension to be turned on.
  section {
    margin-top: -50px;
    padding-top: 50px;

    // Prevents larger content like tables from forcing the page to be wider
    overflow: hidden;
  }

  // Position heading landing spot correctly
  h1, h2, h3, h4, h5, h6 {
    margin-top: 25px;
  }

  // Headings that occur as the first item in the page
  // should not have a top margin.
  > section:first-child {
    margin-top: -70px;
    padding-top: 70px;

    > h1:first-child,
    > h2:first-child,
    > h3:first-child,
    > h4:first-child,
    > h5:first-child,
    > h6:first-child {
      margin-top: 0;
    }
  }

  .caption {
    margin-top: -0.8em;
    font-size: 0.9em;
    font-weight: $thin-font-weight;
    font-style: italic;
    color: #666;
  }
}

.documentation-edit {
  text-align: right;
  line-height: 2em;

  i.fa {
    vertical-align: middle;
    color: #666;
  }
}

